  setItemsPerPage(value): void {
    this.itemsPerPage = value;
    this.totalPages = Math.ceil(this.avisos.length / this.itemsPerPage);
    this.hidePagination = this.totalPages < 2 || isNaN(this.totalPages) ? true : false;
    this.setPageList();
  }

